  problem2: "Have you had the experience of not knowing what to pack <br/>Or not bringing enough clothes for photos <br/>Or buying stuff that youll only need once <br/>Or packing too much unnecessary stuff?",
  problem3: "If yes, Packed is the right solution for you!",
