  gr_goal: "<b>GOAL</b><br/>Explore what people value for traveling, understand how they usually plan for a trip, identify pain points and opportunities.",
  gr_method: "<b>METHOD</b><br/>Desk research<br/>Semi-structured interview<br/>Card Sorting<br/>Draw your experience",
  gr_p: "<b>PARTICIPANT</b><br/>7 participants, all of whom are regular travelers",
  gr: "Since our focus is on peoples experience, I decided to use the method of draw your experience, coupled with interviews and card sorting.<br/><br/> During the interview, we let participants draw their most memorable travel experience, preparation process and ask follow-up questions. It allowed us to dive deeper into peoples values and motivation in addition to their behaviors.",
  grimg: packed_gr,
  insight1: "Combining learnings from desk research and preliminary interview, we identified the following insights to guide our ideation session:",
