import React, { Component } from 'react';
import { H1, H3, H5, P, Subtitle, NextBtn, Highlights, Tag, Title} from '../css/fonts.js';
import { OuterContainer, ShContainer, WholeRow, OuterFlexBox} from '../css/container.js';
import { kloaContent } from '../content.js';
import Navbar from '../components/Navbar';
import Footer from '../components/Footer';
import Image from '../components/Image';
import { CgArrowLongRight } from "react-icons/cg";


class Kloa extends Component {
  render() {
    return (
      <div>
      <OuterContainer>
        <ShContainer>
        <Navbar/>
        <br/><br/><br/><br/>
        <WholeRow>
          <img src={kloaContent.logoimg} alt={kloaContent.logoimg} style={{"width":"25%"}}/>
        </WholeRow>
        <WholeRow>
          <Subtitle theme={{color:"#EE0800"}}>Overview</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: kloaContent.overview }}/><br/>
        <OuterFlexBox>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.role }}/>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.team }}/>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.time }}/>
        </OuterFlexBox>
        </WholeRow>
        <WholeRow>
          <Title>Research Process</Title>
          <Subtitle theme={{color:"#EE0800"}}>Align research goals with business goals</Subtitle>
          <OuterFlexBox>
            <H5 theme={{widthM:"46%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.bgoal }}/>
            <H5 theme={{widthM:"46%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.rgoal }}/>
          </OuterFlexBox>
        </WholeRow>
        <div>
          <Subtitle theme={{color:"#EE0800"}}>Desk research</Subtitle>
          <H5 dangerouslySetInnerHTML={{ __html: kloaContent.deskresearch }}/><br/>
        </div>
        <Image src={kloaContent.cmodelimg} alt={kloaContent.cmodelimg} style={{"width":"100%"}}/>
        <WholeRow>
        <Subtitle theme={{color:"#EE0800"}}>Preliminary interview</Subtitle>
        <OuterFlexBox>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.interview_goal }}/>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.interview_method }}/>
          <H5 light theme={{widthM:"30%", widthS: "100%"}} dangerouslySetInnerHTML={{ __html: kloaContent.interview_p }}/>
        </OuterFlexBox>
        </WholeRow>
        <WholeRow>
          <Title>Current Findings</Title>
          <H3 theme={{color:"#EE0800"}} dangerouslySetInnerHTML={{ __html: kloaContent.findings }}/>
        </WholeRow>
        <WholeRow>
          <Title>Current Learnings and Next Steps</Title>
          <H5 dangerouslySetInnerHTML={{ __html: kloaContent.learning }}/>
          <H5 dangerouslySetInnerHTML={{ __html: kloaContent.nextstep }}/>
        </WholeRow>
        <br/><br/>
        <WholeRow><Footer/></WholeRow>
          <NextBtn  theme={{color:"#EE0800", shadow: "rgba(238, 8, 0, 0.3)"}} to={kloaContent.link}>
            <CgArrowLongRight/>
          </NextBtn>
      </ShContainer>

      </OuterContainer>
      </div>
    );
  }
}

export default Kloa;
